.search-container {
  display: flex;
  align-items: center;
  border: 1px solid #ccc;
  border-radius: 20px;
  padding: 5px 10px;
  width: 100%;
  margin-left: 800px;
  max-width: 250px;
  background: #fff;
  transition: all 0.3s ease-in-out;
}

.search-input {
  flex: 1;
  border: none;
  outline: none;
  padding: 8px;
  font-size: 16px;
  width: 100%;
}

.search-btn {
  background: transparent;
  border: none;
  cursor: pointer;
  color: #555;
  padding: 5px;
}

.search-btn:hover {
  color: #000;
}


@media (max-width: 480px) {
  .search-container {
    width: 90%;
    max-width: none;
  }

  .search-input {
    font-size: 14px;
    padding: 6px;
  }

  .search-btn {
    padding: 3px;
  }
}
